import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

import AuthPage from './views/AuthPage';
import UsersPage from './views/UsersPage';
/* import useUsersElements from './components/UsersList/useUsersElements'; */
import AuthForm from './components/AuthForm';

function App() {
  const ls = localStorage.getItem('token');
/*   const ul = localStorage.getItem('usersList'); */
  const [token, setToken] = useState();
 /*  const { setUsers } = useUsersElements({ token, setToken }); */

  useEffect(() => {
    if (ls) {
      setToken(ls);
      /* setUsers(ul); */
    }
  }, [ls]);
  
  return (
    <Router>
      <div>
        <Switch>
          <Route path='/react-users-app/users'>
            <UsersPage token={token} setToken={setToken} />
          </Route>
          <Route exact path='/react-users-app'>
            <AuthPage token={token} setToken={setToken} />
          </Route>
          <Route path='*' component={AuthForm} />
        </Switch>
      </div>
    </Router>
  );
}

export default App;
